@import '~node_modules/modern-normalize/modern-normalize.css';

section {
    max-width: 480px;
    margin: 0 auto;
    padding-top: 84px;
    padding-bottom: 84px;
    padding-left: 31px;
    padding-right: 31px;
}

button {
    width: 160px;
    height: 40px;
    background-color: transparent;
    border: 1px solid $accent-color-orange;
    border-radius: 25px;
    @include font($font-Open-Sans, $dark-text-svg-color, 600, 12px, 1.33, 0.09);
}

h2 {
    @include font($font-Raleway, $text-color-2, 700, 40px, 1.17, 0.05);
    margin-bottom: 20px;
}

h3 {
    @include font($font-Open-Sans, $text-color-1, 600, 11px, 1.36, 0.1);
    margin-bottom: 20px;
    &::before {
        display: inline-block;
        vertical-align: super;
        contain: content;
        content: ' ';
        width: 60px;
        height: 1px;
        margin-right: 20px;
    }
}